"use client";
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import {
  Box,
  Button,
  Input,
  Heading,
  Alert,
  VStack
} from '@chakra-ui/react';

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const router = useRouter();

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError('');
    const res = await fetch('/api/auth/login', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email, password }),
    });
    if (res.ok) {
      router.push('/dashboard');
    } else {
      setError('Invalid credentials');
    }
  }

  return (
    <Box minH="100vh" bg="#f7f8fa" display="flex" alignItems="center" justifyContent="center">
      <Box bg="#fff" p={8} rounded="lg" boxShadow="lg" w="full" maxW="md">
        <form onSubmit={handleSubmit}>
          <VStack spacing={6} align="stretch">
            <Heading size="lg" textAlign="center">Login</Heading>
            <Box>
              <Input
                type="email"
                value={email}
                onChange={e => setEmail(e.target.value)}
                placeholder="Email"
                autoComplete="email"
                isRequired
                size="lg"
              />
            </Box>
            <Box>
              <Input
                type="password"
                value={password}
                onChange={e => setPassword(e.target.value)}
                placeholder="Password"
                autoComplete="current-password"
                isRequired
                size="lg"
              />
            </Box>
            {error && (
              <Alert status="error">
                {error}
              </Alert>
            )}
            <Button colorScheme="blue" type="submit" size="lg" w="full">
              Login
            </Button>
          </VStack>
        </form>
      </Box>
    </Box>
  );
}
